import React, { useState, useEffect  } from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import {PanelHeaderSimple} from '@vkontakte/vkui';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import {List} from '@vkontakte/vkui'
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import HeaderDrop from '../components/HeaderDrop'

import Icon28BombOutline from '@vkontakte/icons/dist/28/bomb_outline'; // для удаления
import url from "../urls";


const Toms = ({ id,s_id,onTomClick,setActiveModal, setActivePanel,setPopout ,UserData, toms, setToms }) => {
    const [onDel,setDel] = useState(false);


    useEffect( ()=>
    {             
        console.log("Появился");
        fetchData();
    },[toms]);
    async function fetchData()  
    {
        // console.log(s_id);
        let i = await fetch(`${url}/get_subject_toms?id=`+s_id);
        let Toms = await i.json();
        // console.log(toms);
        let items=[];
        for(let item of Toms)
        {              
            // console.log(item);
            let r =await fetch(`${url}/get_tom?id=`+item);
            let resp = await r.json(); 
            items.push(resp);            
        } 
        setPopout(null);            
        if(s(items) !== s(toms)) setToms(items);
    }

    const s =(obj) =>
    {
        return(JSON.stringify(obj))
    }

    async function del(e)
    {
        console.log(e.currentTarget.dataset.t_id);          
        e.preventDefault();      
        let p = await fetch(`${url}/del_tom?id=${e.currentTarget.dataset.t_id}`);
        fetchData();
    }   

    return(
        <Panel separator={false} id={id}>
            <HeaderDrop Head="Разделы" setActiveModal={setActiveModal} UserData={UserData} setActivePanel={setActivePanel} setDel={setDel} onDel={onDel} modal="add-tom"/>            
            <List>
                {toms !=null ?
                    toms.map(item =><Cell key={item.name} before={onDel?<Icon28BombOutline/>:null} data-t_id={item._id} expandable onClick={onDel? del: onTomClick}>{item.name}</Cell>) :
                    <Div/>
                }                
            </List>
        </Panel>
        );
};

export default Toms;

