import React, { useState, useEffect  } from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import {PanelHeaderSimple, ScreenSpinner} from '@vkontakte/vkui';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import {List,PanelHeaderContext,PanelHeaderContent} from '@vkontakte/vkui'
import Div from '@vkontakte/vkui/dist/components/Div/Div';

import Icon24Done from '@vkontakte/icons/dist/24/done';
import HeaderDrop from '../components/HeaderDrop'
import url from "../urls";


import Icon28BombOutline from '@vkontakte/icons/dist/28/bomb_outline'; // для удаления
const Subjects = ({ id,onSubjectClick,setActiveModal,subjs,setPopout,UserData, setSubjects}) => {
    const [onDel,setDel] = useState(false);
    useEffect( ()=>
    {   
        fetchData();
    },[subjs]);

    async function fetchData()  
        {
            let r =await fetch(`${url}/get_subjects`);
            let resp = await r.json();    
            setPopout(null);            
            if(s(resp.items) !== s(subjs)) setSubjects(resp.items);
        }

    const s =(obj) =>
    {
        return(JSON.stringify(obj))
    }

    async function del(e)
    {
      e.preventDefault();      
      let d = await fetch(`${url}/del_subject?id=${e.currentTarget.dataset.s_id}`)
      fetchData();
    }

    return(
        <Panel separator={false} id={id}>
            <HeaderDrop Head="Предметы" setActiveModal={setActiveModal} UserData={UserData} setDel={setDel} onDel={onDel} modal="add-subject"/>            
            <List>
            {subjs !=null ?
                    subjs.map(item =><Cell before={onDel?<Icon28BombOutline />:null}key={item.name} data-s_id={item._id} expandable onClick={onDel? del :onSubjectClick}>{item.name}</Cell>) :
                    <Div/>
                }                
            </List>
        </Panel>);
};

export default Subjects;

