import React, {  useState, useEffect, Component, componentDidMount } from 'react';

import ModalPage from '@vkontakte/vkui/dist/components/ModalPage/ModalPage'
import ModalRoot from '@vkontakte/vkui/dist/components/ModalRoot/ModalRoot'
import ModalPageHeader from '@vkontakte/vkui/dist/components/ModalPageHeader/ModalPageHeader'
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton'
import FormLayout from '@vkontakte/vkui/dist/components/FormLayout/FormLayout'
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import InfoRow from '@vkontakte/vkui/dist/components/InfoRow/InfoRow'

import UserList from '../components/usersList';
import CommentSection from '../components/commentSection';

import {Cell,SimpleCell,Header,List,FormLayoutGroup,Textarea, Input, Group } from '@vkontakte/vkui';

import {Spinner,Div,Alert,FixedLayout} from '@vkontakte/vkui';

import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Done from '@vkontakte/icons/dist/24/done';
import Icon24Send from '@vkontakte/icons/dist/24/send';

import url from "../urls";




const TaskInfo = ({activeModal,setActiveModal,ts_id,t_id,s_id,setts_id,fetchedUser,fetchSubj, fetchToms,fetchTasks, setPopout, UserData}) => {    
    const closeModal = () =>{
        setts_id(null);
		setActiveModal(null);
    } 
    
    async function doneTask() 
    {
        setts_id(null);
        setActiveModal(null);        
        let t = await fetch(`${url}/done_task?id=${ts_id}&user=${fetchedUser.id}`);
        let task = await t.json(); 
        fetchTasks();
    }

    const closePopout = () =>
    {
        setPopout(null);
    }   
    
    async function AddSubj() 
    {
        if(UserData.isHead)
        {
            if(document.getElementById("SubName").value.length == 0) setPopout(<Alert onClose={closePopout}>
            <h2>Ошибка</h2>
            <p>Пустое имя</p>
          </Alert>)
          else{
        setActiveModal(null);        
        let t = await fetch(`${url}/add_subject?name=${document.getElementById("SubName").value}`);
        fetchSubj();
          }
        }
        else setPopout(<Alert onClose={closePopout}>
            <h2>Ошибка</h2>
            <p>Нет прав</p>
          </Alert>)
        
    }
    async function AddTom() 
    {
        if(UserData.isHead)
        {
        if(document.getElementById("TomName").value.length == 0) setPopout(<Alert            
            onClose={closePopout}
          >
            <h2>Ошибка</h2>
            <p>Пустое имя</p>
          </Alert>)
          else{
        setActiveModal(null);  
        let t = await fetch(`${url}/add_subject_tom?id=${s_id}&name=${document.getElementById("TomName").value}`);  
        fetchToms(); 
          } 
        }
        else setPopout(<Alert onClose={closePopout}>
            <h2>Ошибка</h2>
            <p>Нет прав</p>
          </Alert>)    
    }
    async function AddTask() 
    {
        if(UserData.isHead)
        {
        let data = {
            name: document.getElementById("TaskName").value,
            bounty: document.getElementById("Bounty").value,
            about: document.getElementById("Discript").value,
            deadline: document.getElementById("Deadline").value,
        }
        if(data.name.length ==0 || data.bounty.length ==0 || data.about.length ==0) setPopout(<Alert            
        onClose={closePopout}
        >
        <h2>Ошибка</h2>
        <p>Есть незаполненое поле</p>
      </Alert>)
      else
      {
        if(data.deadline.length == 0) data.deadline=0;      
        setActiveModal(null);          
        console.log(data);
        let t = await fetch(`${url}/add_tom_task?id=${t_id}&name=${data.name}&bounty=${data.bounty}&about=${data.about}&deadline=${data.deadline}`);  
        fetchTasks(); 
        } 
    }
    else setPopout(<Alert onClose={closePopout}>
        <h2>Ошибка</h2>
        <p>Нет прав</p>
      </Alert>)    
    }


    const [task,setTask]=useState(null);

    useEffect(()=>{
        async function fetchData()  
        {
            let t = await fetch(`${url}/get_task?id=`+ts_id);
            let task = await t.json();          
            setTask(task);
        }
        if(activeModal=="task_info") fetchData();
    });

    const Isdone = () =>
    {
        if(fetchedUser != null && task != null && task != "-2")
        {            
            let user = task.doneers.find(item => item == fetchedUser.id)            
            if(user == null) return true;
            else return false;
        }
    }

    async function AddComment ()    
    {
        console.log(document.getElementById("commentary").value);
        let text = document.getElementById("commentary").value;
        let r = await fetch(`${url}/comment_task?id=${ts_id}&user=${fetchedUser.id}&text=${text}`);
        let res = await r.json(); 
         
        document.getElementById("commentary").value='';
    }

	const agree = (item) =>{
		
	}
    return (
        <ModalRoot activeModal={activeModal}>
				<ModalPage id ="task_info"
                onClose={closeModal}
                dynamicContentHeight={true}
                settlingHeight={100}
				header={
					<ModalPageHeader
                right={Isdone() ? <PanelHeaderButton onClick={doneTask}>Сдать </PanelHeaderButton> : <p/>} >
                        {task !=null && task != "-2" ? task.name : "Loading..."}
                    </ModalPageHeader>
				}>                    
                    {task !=null && task != "-2" ? 
                        
                    <FormLayout>                        
                        <SimpleCell disabled={true} multiline>{task.discription}</SimpleCell>
                        <SimpleCell disabled={true} description="За первое выполнение">Награда: {task.bounty} rep</SimpleCell>                               
                        <UserList users={task.doneers}/>
                        <CommentSection comments={task.comments}/>
                        <Textarea id="commentary" placeholder="Ваш комментарий"/>
                        <Button onClick={AddComment} size="l">Добавить</Button> 
                    </FormLayout    >                                                           
                    :
                    <Div> 
                        <Spinner size="large" style={{ marginBottom: 30 }} />
                    </Div>}
				</ModalPage>

                <ModalPage id ="add-subject"
                onClose={closeModal}
                dynamicContentHeight={true}
				header={
					<ModalPageHeader>
                        Добавить
                    </ModalPageHeader>
				}>                    
                    {true ? 

                    <FormLayout>
                       <Input id="SubName"/>  
                       <Button onClick={AddSubj} size="xl">Добавить</Button>
                    </FormLayout>:
                    <Div> 
                        <Spinner size="large" style={{ marginBottom: 10 }} />
                    </Div>}
				</ModalPage>
                <ModalPage id ="add-tom"
                onClose={closeModal}
                dynamicContentHeight={true}
				header={
					<ModalPageHeader>
                        Добавить
                    </ModalPageHeader>
				}>                    
                    {true ? 

                    <FormLayout>
                       <Input id="TomName"/>  
                       <Button onClick={AddTom} size="xl">Добавить</Button>
                    </FormLayout>:
                    <Div> 
                        <Spinner size="large" style={{ marginBottom: 10 }} />
                    </Div>}
				</ModalPage>
                <ModalPage id ="add-task"
                onClose={closeModal}
                dynamicContentHeight={true}
				header={
					<ModalPageHeader>
                        Добавить
                    </ModalPageHeader>
				}>                                        
                    <FormLayout>
                        <Input id="TaskName" top={"Название"}/>
                        <Input id="Bounty" top={"Награда"}/>
                        <Textarea id="Discript" top={"Описание"}/>
                        <Input id="Deadline" type={"date"} top={"Дед-лайн"}/>
                        <Button onClick={AddTask} size="xl">Добавить</Button>
                    </FormLayout>                    
				</ModalPage>
                <ModalPage id ="q"
                onClose={closeModal}
                dynamicContentHeight={true}
				header={
					<ModalPageHeader>
                        Добавить
                    </ModalPageHeader>
				}>                                        
                    <FormLayout>
                        <Cell>Дарова</Cell>
                        <UserList users={[]}/>
                        <CommentSection comments={[]}/>
                        <Input id="TaskName" top={"Название"}/>
                        <Input id="Bounty" top={"Награда"}/>
                        <Textarea id="Discript" top={"Описание"}/>
                        <Input id="Deadline" type={"date"} top={"Дед-лайн"}/>
                        <Button onClick={AddTask} size="xl">Добавить</Button>
                    </FormLayout>                   
				</ModalPage>
		</ModalRoot>
    );

}

export default TaskInfo;