import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';

import bridge from '@vkontakte/vk-bridge';

import {Spinner,PanelHeaderSimple, PanelHeaderContent,PanelHeaderContext, Header,Div, List} from '@vkontakte/vkui';

import Icon24Dropdown from '@vkontakte/icons/dist/24/dropdown';
import Icon24Back from '@vkontakte/icons/dist/24/back';


const HeaderDrop = ({Head,setActiveModal,setActivePanel,UserData,modal,setDel,onDel}) => {
    const [contextOpened,setcontextOpened] = useState(false);

    const Switch= () =>
    {
        setcontextOpened(!contextOpened)
    }

    const onBack = e =>
    {
        e.preventDefault();	
        console.log('обратно...'); 
        switch (Head) {
          case "Задания":
              setActivePanel("tome");
            break;
          case "Разделы":
              setActivePanel("subj");            
            break;
          default:
            break;
        }        
    }

    const onAddClick = e =>
    {
        e.preventDefault();	
        console.log('Сейчас добавим');
        Switch();        
        setActiveModal(modal);
    }    
    const onDelClick = e =>
    {
        e.preventDefault();	
        console.log('Удаляем...'); 
        Switch();
        setDel(!onDel);     
    }

    return(<Div style={{padding:0}}>
        <PanelHeaderSimple left={Head == "Предметы"?null:<Icon24Back onClick={onBack}/>}><PanelHeaderContent
              aside={UserData != null && UserData.isHead ? <Icon24Dropdown style={{ transform: `rotate(${contextOpened ? '180deg' : '0'})` }} /> : null}
              onClick={UserData != null && UserData.isHead ? Switch:()=>{}}
            >
              {Head}
            </PanelHeaderContent></PanelHeaderSimple >
            <PanelHeaderContext opened={contextOpened} onClose={()=>{}}>
            <List>
              <Cell onClick={onAddClick}                                            
              >
                Добавить
              </Cell>
              <Cell  onClick={onDelClick}              
              >
                {onDel?"Закончить удаление":"Удалить"}
              </Cell>
            </List>
          </PanelHeaderContext></Div>)
};

export default HeaderDrop;





