import React, { useState, useEffect  } from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import {PanelHeaderSimple} from '@vkontakte/vkui';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import {List} from '@vkontakte/vkui'
import Div from '@vkontakte/vkui/dist/components/Div/Div';

import Icon16Done from '@vkontakte/icons/dist/16/done';
import HeaderDrop from '../components/HeaderDrop'
import Icon28BombOutline from '@vkontakte/icons/dist/28/bomb_outline'; // для удаления

import url from "../urls";



const TasksList = ({ id,t_id,onTaskClick, fetchedUser,setActivePanel,setPopout, UserData, setActiveModal, tasks, setTasks}) => {
    const [onDel,setDel] = useState(false);
    
    useEffect( ()=>
    {               
        console.log("На задании");
        console.log(tasks);     
        fetchData();
    },[tasks]);

    async function fetchData()  
        {
            let i = await fetch(`${url}/get_tom_tasks?id=`+t_id);
            let Tasks = await i.json();
            let items=[];
            for(let item of Tasks)
            {                
                let r =await fetch(`${url}/get_task?id=`+item);
                let resp = await r.json(); 
                items.push(resp);
            }    
            setPopout(null);            
            if(s(items) !== s(tasks)) setTasks(items);
            setPopout(null);            
        }

    async function del(e)
    {
      e.preventDefault();      
      let d = await fetch(`${url}/del_task?id=${e.currentTarget.dataset.ts_id}`)
      fetchData();
    }

    const clean = () =>
    {
        setTasks(null);
    }

    const s =(obj) =>
    {
        return(JSON.stringify(obj))
    }

    const Isdone = (doneers) =>
    {
        if(fetchedUser != null)
        {            
            let user = doneers.find(item => item == fetchedUser.id)            
            if(user == null) return true;
            else return false;
        }
    }

    return(
        <Panel separator={false} id={id}>
            <HeaderDrop Head="Задания" setActiveModal={setActiveModal} UserData={UserData} setActivePanel={setActivePanel} setDel={setDel} onDel={onDel} modal="add-task"/>            
            <List>
                {tasks !=null ?
                    tasks.map(item =><Cell key={item.name}  data-ts_id={item._id} expandable onClick={onDel? del: onTaskClick} before={onDel ? <Icon28BombOutline/> :Isdone(item.doneers)? null :<Icon16Done/>}>{item.name}</Cell>) :
                    <Div/>
                }                
            </List>
        </Panel>
        );
};

export default TasksList;

