import React, { useState, useEffect  } from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import {PanelHeaderSimple,PanelHeader} from '@vkontakte/vkui';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import {List,PanelHeaderContext,PanelHeaderContent,Avatar} from '@vkontakte/vkui'
import Div from '@vkontakte/vkui/dist/components/Div/Div';

import Icon24Done from '@vkontakte/icons/dist/24/done';
import HeaderDrop from '../components/HeaderDrop'
import url from "../urls";


import Icon28BombOutline from '@vkontakte/icons/dist/28/bomb_outline'; // для удаления
const Members = ({id}) => {
    const [users,setUsers] = useState(null);
    useEffect( ()=>
    {     
        fetchData();
    },[users]);

    const s =(obj) =>
    {
        return(JSON.stringify(obj))
    } 

    async function fetchData()  
        {
            let mem =await fetch(`${url}/get_members`);
            let members = await mem.json();   
            let ids;
            if(members.length>0)
            {
                ids=String(members[0].vk_id);
                for(let member of members)
                {       
                    ids+=","+String(member.vk_id);
                } 
                let vk_in = await fetch(`${url}/get_users_vk_info?ids=${ids}`)
                let vk_info = await vk_in.json();
                vk_info = vk_info.response;
                let data=[];               
                for(let user of vk_info)
                {
                    data.push({
                        firstName: user.first_name,
                        lastName: user.last_name,
                        photo: user.photo_50,
                        rep: members.find(membe => membe.vk_id == user.id).rep
                    })
                }            
                data.sort((a,b)=>{
                    if(a.rep>b.rep) return -1;
                    else
                    if(b.rep<a.rep) return 1;
                    else return 0;
                });    
                if(s(users) !== s(data)) setUsers(data);
            } 

        }

       

    return(
        <Panel id={id}>
            <PanelHeader>
                Пользователи
            </PanelHeader>
            <List>
            {users ? users.map(user => 
                <Cell key={user.lastName+user.firstName}
                  before={<Avatar src={user.photo} />}
                  size="l"
                  description={"Репутация: "+user.rep}                  
                >
                  {user.lastName} {user.firstName}</Cell>):null}
            </List>
        </Panel>);
};

export default Members;

