import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';

import bridge from '@vkontakte/vk-bridge';

import {Spinner, Header,Div, List} from '@vkontakte/vkui';

import url from "../urls";


const CommentSection = ({ comments }) => {
    const [UsersData,setUserData] = useState(null);    
    useEffect(()=>
    {
        // Получить инфу всех пользователей
        async function fetchData()  
        {
            let ids;
            if(comments.length > 0)
            ids = comments[0].id;
            for(let comment of comments)
            {
                ids+=","+comment.id;
            }
            let result = null;
            if(comments.length >0)
            {
                let t = await fetch(`${url}/get_users_vk_info?ids=${ids}`);
                let r = await t.json();
                result = r.response; 
            }
            else result = [];           
            // Присвоить в хук             
            setUserData(result);
        }
        fetchData(); 

    },[]);
    return(
        <Group header={<Header mode="secondary">Коментарии</Header>}>
        <List>
            {UsersData != null ? 
                (UsersData.length > 0  ? 
            comments.map((comment) => { 
                let user = UsersData.find(u => u.id == comment.id);
                if (user === undefined) return(null);
                return(<Cell 
                    key={comment.text} 
                    before={<Avatar size={48} src={user.photo_50} />} 
                    size="l"
                description={<Cell multiline>{comment.text}</Cell>}>
                {user.last_name} {user.first_name}
                </Cell>)})
            :
            <Cell>Здусь пусто</Cell>)
            :
            <Spinner size="large" />
            }          
        </List>
      </Group>);
};
// comments.find(item => item.id == UsersData.id).text
{/* <Cell key={user.id} before={<Avatar src={user.photo_50}/>}>{user.last_name} {user.first_name}</Cell> */}

export default CommentSection;
