import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';

import bridge from '@vkontakte/vk-bridge';

import {Spinner, Header, List} from '@vkontakte/vkui';

import url from "../urls";



const UserList = ({ users }) => {
    const [UsersData,setUserData] = useState(null);    
    useEffect(()=>
    {
        // Получить инфу всех пользователей
        async function fetchData()  
        {
            let ids = users[0];
            for(let user of users)
            {
                ids+=","+user;
            }
            let result = null;
            if(users.length >0)
            {
                let t = await fetch(`${url}/get_users_vk_info?ids=${ids}`);
                let r = await t.json();
                result = r.response; 
            }
            else result = [];           
            // Присвоить в хук             
            setUserData(result);
        }
        fetchData(); 

    },[]);
    return(
        <Group header={<Header mode="secondary">Сделали</Header>}>
        <List>
            {UsersData != null ? 
                (UsersData.length > 0 ? 
            UsersData.map(user =><Cell key={user.id} before={<Avatar src={user.photo_50}/>}>{user.last_name} {user.first_name}</Cell>)
            :
            <Cell>Будь первым!</Cell>)
            :
            <Spinner size="large" />
            }          
        </List>
      </Group>);
};

export default UserList;
