import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import {Epic,Tabbar,TabbarItem} from '@vkontakte/vkui'
import {Panel,
		PanelHeader} from '@vkontakte/vkui';

import Icon28BrainOutline from '@vkontakte/icons/dist/28/brain_outline'; // Задания
import Icon28UserOutline from '@vkontakte/icons/dist/28/user_outline'; // Профиль
import Icon28Users3Outline from '@vkontakte/icons/dist/28/users_3_outline'; // Пользователи

import Profile from './panels/Profile';

import Tasks from './views/Tasks';
import Members from './panels/Members';

import url from "./urls";

import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

const App = () => {
	const [activeStory,setActiveStory] = useState('me');
	const [activePanel, setActivePanel] = useState();
	const [fetchedUser, setUser] = useState(null);
    const [UserData,setUserData] = useState(null);    
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
 
	useEffect(() => {
		bridge.send("VKWebAppInit", {});
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			const find = await fetch(`${url}/get_member?id=`+user.id);
			const finded = await find.json();
			if(finded == "-1")
			{    
				const add = await fetch(`${url}/add_member?vk_id=`+user.id);
				const find = await fetch(`${url}/get_member?id=`+user.id);
				const finded = await find.json();
				if(finded != "-1") setUserData(finded);				
			}
			else setUserData(finded);	
			setUser(user);
		}			
			fetchData();
		
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	const onStoryChange = e =>{	
		setActiveStory(e.currentTarget.dataset.story);
	};

	return (
		<Epic activeStory={activeStory} tabbar={
		  <Tabbar>
			<TabbarItem
			  onClick={onStoryChange}
			  selected={activeStory === 'me'}
			  data-story="me"
			  text="Профиль"
			><Icon28UserOutline /></TabbarItem>
			<TabbarItem
			  onClick={onStoryChange}
			  selected={activeStory === 'task'}
			  data-story="task"
			//   label="12"
			  text="Задания"
			><Icon28BrainOutline /></TabbarItem>
			<TabbarItem
			  onClick={onStoryChange}
			  selected={activeStory === 'members'}
			  data-story="members"
			  text="Пользователи"
			><Icon28Users3Outline /></TabbarItem>			
		  </Tabbar>
		}>
		  <View id="me" activePanel="me">
			<Profile id="me" fetchedUser={fetchedUser} setUserData={setUserData} UserData={UserData}/>			  			
		  </View>
		  <Tasks fetchedUser={fetchedUser} UserData={UserData} id="task"/>		 
		<View id="members" activePanel="members">
			<Members id="members"/>			
		</View>		  
		</Epic>
	  );
}

export default App;

