import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import {PanelHeaderSimple} from '@vkontakte/vkui';
import {View,ScreenSpinner} from '@vkontakte/vkui';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';

import Subjects from '../panels/Subjects';
import Toms from '../panels/Tom';
import TasksList from '../panels/Tasks';
import TaskInfo from '../modals/TaskInfo';
import url from "../urls";


const Modal_page_task = "task_info";

const Tasks = ({ id, fetchedUser, UserData }) => {
    const [activePanel,setActivePanel] = useState("subj"); 
    const[activeModal,setActiveModal] = useState(null);
    const[popout,setPopout] = useState(null);
    
    const [toms,setToms] = useState(null);
    const [subjs,setSubjects] = useState(null);
    const [tasks,setTasks] = useState(null);


    const [s_id,sets_id] = useState(null);  
    const [t_id,sett_id] = useState(null);  
    const [ts_id,setts_id] = useState(null); 
    useEffect(()=>
    {
        setPopout(<ScreenSpinner/>);        
    },[]);       
    
    async function fetchSubj()  
    {   let r =await fetch(`${url}/get_subjects`);
        let resp = await r.json(); 
        if(s(resp.items) !== s(subjs)) setSubjects(resp.items);
    }

    async function fetchToms()  
    {
        let i = await fetch(`${url}/get_subject_toms?id=`+s_id);
        let Toms = await i.json();
        let items=[];
        for(let item of Toms)
        {              
            let r =await fetch(`${url}/get_tom?id=`+item);
            let resp = await r.json(); 
            items.push(resp);            
        } 
        if(s(items) !== s(toms)) setToms(items);
    }

    async function fetchTasks()  
        {
            let i = await fetch(`${url}/get_tom_tasks?id=`+t_id);
            let Tasks = await i.json();
            let items=[];
            for(let item of Tasks)
            {                
                let r =await fetch(`${url}/get_task?id=`+item);
                let resp = await r.json(); 
                items.push(resp);
            } 
            if(s(items) !== s(tasks)) setTasks(items);
        }

    const onSubjectClick = e =>{
        e.preventDefault();	
        console.log('Вперёд к разделам!');
        sets_id(e.currentTarget.dataset.s_id)
        setToms(null);
        setPopout(<ScreenSpinner/>);                 
        setActivePanel("tome");
	      }; 

    const onTomClick = e =>{
          e.preventDefault();	
          console.log('Вперёд к заданиям!');
          sett_id(e.currentTarget.dataset.t_id)
          setTasks(null);
          setPopout(<ScreenSpinner/>);        
          setActivePanel("task");          
          };    

    const onTaskClick = e =>
    {
      e.preventDefault();	
      console.log('Вперёд к информации о задании!');
      setActiveModal(Modal_page_task);
      setts_id(e.currentTarget.dataset.ts_id)
    }

    const s =(obj) =>
    {
        return(JSON.stringify(obj))
    }

    return(
        <View header={false} popout={popout} id={id} activePanel={activePanel} modal={
        <TaskInfo fetchedUser={fetchedUser} 
                  s_id={s_id} 
                  t_id={t_id} 
                  activeModal={activeModal} 
                  setts_id={setts_id} 
                  ts_id={ts_id}
                  fetchToms={fetchToms} 
                  fetchSubj={fetchSubj}
                  fetchTasks={fetchTasks}
                  UserData={UserData}
                  setPopout={setPopout}
                  setActiveModal={setActiveModal}/>}>          
          <Subjects setPopout={setPopout} onSubjectClick ={onSubjectClick} id="subj" UserData={UserData} subjs={subjs} setSubjects={setSubjects} setActivePanel={setActivePanel} setActiveModal={setActiveModal}/>        
          <Toms id="tome"  setPopout={setPopout} onTomClick={onTomClick} s_id={s_id} UserData={UserData} toms={toms} setToms={setToms} setActivePanel={setActivePanel} setActiveModal={setActiveModal}/>
          <TasksList setPopout={setPopout} fetchedUser={fetchedUser} id="task" UserData={UserData} onTaskClick={onTaskClick} t_id={t_id} tasks={tasks} setTasks={setTasks} setActivePanel={setActivePanel} setActiveModal={setActiveModal}/>                    
        </View>);
};

export default Tasks;
